import back from '@/components/back'

export default {
    components:{
        back
    },
	data () { //引入PopupPicker组件 引入提现路由
		return {
			DrId: this.$route.params.id,
			icon: 'circle',
			CashAmount: 0, //提现金额
			DrBankCardId: null, //银行卡id
			show: false,
			switchTxt: '',
			verify: false,
			verifyTxt: '',
			BankName: '',
			CardNo: '',
			bankStart: false
		}
	},
	methods: {
		checkboxFn () {
			//this.icon === 'circle' ? this.icon = 'success' : this.icon = 'circle'
			this.show = true
		},
		esc () {
			this.icon = 'circle'
			this.show = false
		},
		addCard () {
			this.$router.push({
				path: '/Center/addCard',
				name: 'addCard',
				params: {
					id: this.DrId,
					start: 1, //新增
					DrBankCardId: null
				}
			})
		},
		grouapsDeteleDialog () {
			this.icon = 'success'
			this.show = false
		},
		cardFn () {
			this.$get('/PlatFormAPI/Doctor/QueryDoctorBankCardByDrId',{ //提现申请
				DrId: this.DrId
			}).then(o => {
				if(o && o != null){
					this.bankStart = true
					this.BankName = o.BankName
					this.DrBankCardId = o.DrBankCardId
					this.CardNo = o.CardNo
				}
			})
		},
		submitFn () {
			if(this.CashAmount === 0){
				//this.switchTxt = '提现金额不能是0'
				this.verifyTxt = '金额不能是0'
				this.verify = true
				return
			}
			if(this.icon === 'circle'){
				//this.switchTxt = '提现金额不能是0'
				this.verifyTxt = '协议未勾选'
				this.verify = true
				return
			}
			
			this.$post('/PlatFormAPI/Doctor/AddDoctorCash',{ //提现申请
				DrId: this.DrId,
				DrBankCardId: this.DrBankCardId,
				CashAmount: this.CashAmount
			}).then((o,v) => {
				if(o){
					this.$router.push({
						path: '/Center/depositSucceed',
						name: 'depositSucceed',
						query: {
							sss: this.CardNo,
							kkk: this.CashAmount
						},
					})
				}
			})
		}
	},
	created(){
		this.cardFn()
	}
}



